.product {
  &__frame {
    @include frame("rect", 100%);
    @include frame("zoom");
  }

  &__title {
    font-size: 16px;
    font-weight: 700;
    margin: 12px 0 0;
  }

  &__desc {
    @include max-line(2);
    color: #999;
    margin-top: 4px;
  }

  @media (max-width: 767px) {
    &__title {
      margin-top: 8px;
      font-size: 13px;
    }
  }
}

.product-2 {
  &__frame {
    @include frame("rect", 100%);
    @include frame("zoom");
  }

  &__title {
    font-size: 16px;
    font-weight: 700;
    margin: 12px 0 0;
  }

  @media (max-width: 767px) {
    &__title {
      margin-top: 8px;
      font-size: 12px;
    }
  }
}

.news {
  display: flex;
  flex-direction: column;
  height: 100%;

  &__frame {
    @include frame("rect", 75%);
    @include frame("zoom");
  }

  &__body {
    background-color: $light;
    flex-grow: 1;
    padding: 20px;
  }

  &__title {
    font-size: 18px;
    font-weight: 700;
    text-align: center;
    margin-bottom: 8px;
  }

  &__info {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 4px;
  }

  &__desc {
    @include max-line(2);
  }

  @media (max-width: 767px) {
    &__body {
      padding: 12px;
    }

    &__title {
      font-size: 14px;
    }

    &__info {
      font-size: 12px;
    }

    &__desc {
      font-size: 12px;
    }
  }
}

.news-2 {
  &__frame {
    @include frame("rect", 75%);
    @include frame("zoom");
  }

  &__title {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 0;
    margin-top: 12px;
  }

  &__time {
    font-size: 12px;
  }

  &__desc {
    @include max-line(4);
    margin-top: 8px;
  }
}

.news-3 {
  &__title {
    font-size: 16px;
    margin: 0;
  }
}
