.gallery-slider,
.news-slider,
.product-2-slider {
  position: relative;

  &__prev,
  &__next {
    display: flex;
    width: 30px;
    height: 30px;
    justify-content: center;
    align-items: center;
    border: 1px solid #ccc;
    background-color: $light;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: -40px;
    z-index: 10;
    font-size: 18px;
    transform: translateY(-50%);
    cursor: pointer;
    transition: 0.3s;
    opacity: 0;

    &:focus {
      outline: 0;
    }
  }

  &__next {
    left: auto;
    right: -40px;
  }

  &:hover &__prev,
  &:hover &__next {
    opacity: 1;
  }

  @media (max-width: 575px) {
    &__prev {
      left: -15px;
      opacity: 1;
    }

    &__next {
      right: -15px;
      opacity: 1;
    }
  }
}

.gallery-slider {
  &__frame {
    @include frame("rect", 133.33%);
    @include frame("zoom");
  }
}

.client-slider {
  position: relative;
  padding: 0 40px;
  max-width: 800px;
  margin: 0 auto;

  &__prev,
  &__next {
    display: flex;
    width: 30px;
    height: 30px;
    justify-content: center;
    align-items: center;
    border: 1px solid #ccc;
    background-color: $light;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 0;
    z-index: 10;
    font-size: 18px;
    transform: translateY(-50%);
    cursor: pointer;

    &:focus {
      outline: 0;
    }
  }

  &__next {
    left: auto;
    right: 0;
  }

  &__frame {
    @include frame("rect", 66.66%);
  }

  @media (max-width: 575px) {
    padding: 0 20px;

    &__prev {
      left: -15px;
    }

    &__next {
      right: -15px;
    }
  }
}

.banner-slider {
  position: relative;
  z-index: 1;

  &__frame {
    img {
      display: block;
      width: 100%;
    }
  }

  &__pagination {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;

    .swiper-pagination-bullet {
      opacity: 1;
      width: 30px;
      height: 4px;
      background-color: $white;
      border-radius: 0;
      margin: 0 2px;

      &:focus {
        outline: 0;
      }

      &-active {
        background-color: $orange;
      }
    }
  }
}
