.header {
  background-color: $secondary;
  position: relative;
  z-index: 100;

  &__inner {
    display: flex;
    align-items: center;
    position: relative;
    height: 105px;
  }

  &__logo {
    display: inline-flex;
    margin-right: auto;

    img {
      height: 70px;
    }
  }

  &__btn {
    color: $primary;
    border-color: $primary;
    margin-left: 8px;
  }

  @media (min-width: 1200px) {
    margin-top: 60px;

    &__logo {
      margin-right: 0;
    }

    &__btn {
      display: none;
    }
  }
}

.lang {
  display: flex;

  &__item {
    display: flex;

    &:not(:last-child) {
      margin-right: 8px;
    }

    img {
      height: 20px;
    }
  }

  @media (min-width: 1200px) {
    position: absolute;
    top: 8px;
    right: 0;
  }
}

.search {
  padding: 10px 20px 20px;
  border-bottom: 1px solid $border-color;

  .form-control {
    border-radius: 18px 0 0 18px;
    border-color: $border-color;
    border-right: 0;
    padding: 0 16px;
  }

  .input-group-text {
    border-radius: 0 18px 18px 0;
    border-left: 0;
    background-color: #fff;
  }

  @media (min-width: 1200px) {
    width: 250px;
    position: absolute;
    bottom: calc(100% + 30px);
    right: 0;
    transform: translateY(50%);
    padding: 0;
    border: 0;

    .form-control {
      height: 30px;
      border-color: #999;
      line-height: 28px;
    }

    .input-group-text {
      border-color: #999;
    }
  }
}

.h-info {
  &__item {
    border-top: 1px solid $border-color;
    padding: 8px 20px;

    a,
    a:hover {
      color: inherit;
    }
  }

  @media (min-width: 1200px) {
    display: flex;
    position: absolute;
    bottom: calc(100% + 30px);
    left: 0;
    transform: translateY(50%);

    &__item {
      padding: 0;
      border: 0;

      &:not(:last-child) {
        margin-right: 40px;
      }
    }
  }
}
