.footer {
  padding: 20px 0;
  background-color: $secondary;

  &__grid {
    display: grid;
    grid-template-columns: 2fr 2fr 3fr 3fr;
    grid-gap: 20px;
  }

  &__logo-wrapper {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
  }

  &__logo {
    display: inline-flex;

    img {
      height: 70px;
    }
  }

  &__title {
    font-size: 16px;
    font-weight: 400;
    text-transform: uppercase;
    color: $primary;
    margin-bottom: 8px;
  }

  &__contact {
    color: #999;

    a,
    a:hover {
      color: inherit;
    }
  }

  &__copyright {
    color: #999;
    margin-top: 10px;
  }

  @media (max-width: 991px) {
    &__grid {
      grid-template-columns: 1fr 1fr;
    }
  }

  @media (max-width: 767px) {
    &__grid {
      grid-template-columns: 100%;
    }
  }
}

.f-menu {
  list-style: none;
  margin: 10px 0 0;
  padding: 0;

  &__item {
    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }

  &__link {
    font-size: 14px;
    color: #999;

    &:hover {
      color: $primary;
      text-decoration: none;
    }
  }
}

.f-social {
  display: flex;
  margin-top: 16px;

  &__item {
    display: flex;

    &:not(:last-child) {
      margin-right: 10px;
    }

    img {
      width: 24px;
      height: 24px;
      object-fit: cover;
      object-position: 50% 50%;
      border-radius: 50%;
    }
  }
}

.f-form {
  &__label {
    color: #999;
    font-size: 14px;
    margin-bottom: 8px;
  }

  &__wrapper {
    max-width: 250px;
  }

  .form-control {
    border-radius: 18px;
    border: 0;
    background-color: rgba(#fff, 0.5);
    color: $white;
    font-style: italic;
    padding: 0 20px;

    &::placeholder {
      color: #ccc;
    }
  }
}
