.pagination {
  justify-content: center;
  box-shadow: inset 0 1px 0 $border-color;
  margin-bottom: 0;

  .page-item {
    &:not(:last-child) {
      margin-right: 4px;
    }
  }

  .page-link {
    padding: 8px 16px;
    border-radius: 0 !important;
    border: 0;
    position: relative;
    background: 0 !important;
    color: #999;
    font-size: 16px;

    &::before {
      content: "";
      display: none;
      width: 100%;
      border-top: 1px solid #000;
      position: absolute;
      top: 0;
      left: 0;
    }

    &:hover {
      color: #333;

      &::before {
        display: block;
      }
    }
  }

  .page-item.active .page-link {
    color: #000;
    position: relative;

    &::before {
      display: block;
    }
  }
}

.breadcrumb-wrapper {
  border-bottom: 1px solid $secondary;
  padding: 8px 0;
}

.breadcrumb-content {
  border-left: 1px solid $secondary;
  padding-left: 16px;
}

.breadcrumb-title {
  font-size: 16px;
}

.breadcrumb {
  padding: 0;

  &-item {
    font-size: 16px;

    &,
    & + &::before,
    &.active,
    &.active::before,
    a,
    a:hover {
      color: $gray-800;
    }
  }

  &-item.active {
    color: $orange;
  }
}

$phoneRingColor: #e73a35;
.phone-ring {
  display: flex;
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
  color: #fff;
  border-radius: 50%;
  background-color: $phoneRingColor;
  position: fixed;
  z-index: 100;
  bottom: 50px;
  left: 50px;
  box-shadow: 0 5px 20px rgba(#000, 0.3);

  &:hover {
    color: $white;
  }

  &::before {
    content: "";
    display: block;
    width: 250%;
    height: 250%;
    border-radius: 50%;
    border: 1px solid $phoneRingColor;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: phone-circle-animate 1s ease-in-out infinite;
  }

  &::after {
    content: "";
    display: block;
    width: 160%;
    height: 160%;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: -1;
    transform: translate(-50%, -50%);
    background-color: rgba($phoneRingColor, 0.5);
    animation: phone-circle-fill-animate 2s ease-in-out infinite;
  }

  &__icon {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    background-color: rgba($phoneRingColor, 0.7);
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
    animation: phone-icon-animate 1s ease-in-out infinite;
  }

  &__text {
    display: inline-block;
    background-color: rgba($phoneRingColor, 0.7);
    font-size: 14px;
    font-weight: 700;
    line-height: 40px;
    padding: 0 20px 0 60px;
    color: $white;
    border-radius: 20px;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    box-shadow: 0 5px 20px rgba(#000, 0.3);
  }

  &:hover &__text {
    color: $phoneRingColor;
    background-color: rgba(#fff, 0.7);
  }

  @media (max-width: 767px) {
    left: 20px;
    bottom: 20px;

    &__text {
      display: none;
    }
  }
}

@keyframes phone-circle-animate {
  0% {
    transform: translate(-50%, -50%) rotate(0) scale(0.5);
    opacity: 0.1;
  }
  30% {
    transform: translate(-50%, -50%) rotate(0) scale(0.7);
    opacity: 0.5;
  }
  100% {
    transform: translate(-50%, -50%) rotate(0) scale(1);
    opacity: 0.1;
  }
}

@keyframes phone-circle-fill-animate {
  0%,
  100% {
    transform: translate(-50%, -50%) rotate(0) scale(0.7);
    opacity: 0.2;
  }
  50% {
    transform: translate(-50%, -50%) rotate(0) scale(1);
    opacity: 0.2;
  }
}

@keyframes phone-icon-animate {
  0%,
  100% {
    transform: rotate(0) scale(1) skew(1deg);
  }
  10% {
    transform: rotate(-25deg) scale(1) skew(1deg);
  }

  20%,
  40% {
    transform: rotate(25deg) scale(1) skew(1deg);
  }
  30% {
    transform: rotate(-25deg) scale(1) skew(1deg);
  }
  50% {
    transform: rotate(0) scale(1) skew(1deg);
  }
}

.intro {
  padding: 20px 0;
  margin-bottom: 40px;

  &__text-1 {
    font-size: 26px;
    font-weight: 700;
    text-align: center;
    color: $orange;
    margin-bottom: 20px;
  }

  &__text-2 {
    font-size: 20px;
    text-align: center;
    max-width: 450px;
    margin: 0 auto;
  }

  @media (max-width: 767px) {
    margin-bottom: 0;

    &__text-1 {
      font-size: 16px;
      margin-bottom: 10px;
    }

    &__text-2 {
      font-size: 14px;
    }
  }
}

.news-wrapper {
  padding: 40px 0;
}

.about-wrapper {
  max-width: 700px;
  margin: 0 auto;
}

.history {
  &__label {
    margin-bottom: 4px;

    &:not(:last-child) {
      margin-top: 24px;
    }
  }

  &__item {
    display: flex;
    align-items: baseline;
    padding-left: 150px;
  }

  &__year {
    width: 80px;
    padding: 0 16px;
    font-size: 16px;
    position: relative;

    &::before {
      content: "";
      display: block;
      width: 6px;
      height: 6px;
      background-color: #000;
      position: absolute;
      top: 10px;
      left: 0;
    }
  }

  &__story {
    flex: 1;
  }

  @media (max-width: 575px) {
    &__item {
      padding-left: 30px;
    }
  }
}

.about {
  &__text-1 {
    text-align: center;
  }

  &__text-2 {
    text-align: center;
    color: $orange;
    font-size: 20px;
    margin-top: 30px;
  }
}

.contact-addr {
  p {
    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  a,
  a:hover {
    color: $orange;
  }
}

.contact-map {
  height: 400px;
}

.ct-form {
  &__label {
    font-size: 14px;
    margin-bottom: 10px;
  }

  &__table {
    border: 1px solid $border-color;

    td {
      &:first-child {
        text-align: center;
        vertical-align: middle;
        background-color: $light;
      }
    }
  }

  &__footer {
    display: flex;
    justify-content: center;
  }

  &__btn {
    @include btn("shine");
    display: inline-block;
    background-color: $secondary;
    color: $white;
    padding: 0 20px;
    line-height: 40px;
    border: 0;
    font-size: 16px;

    &:hover {
      color: $white;
      text-decoration: none;
    }
  }

  .form-control {
    border: 0;
    background-color: $light;
  }
}

.agent {
  &__label {
    text-align: center;
  }

  &__field {
    background-color: $light;
    padding: 10px 20px;
    border-radius: 21px;
    text-align: center;
    margin-top: 8px;
    max-width: 690px;
    margin: 8px auto 0;
  }

  &__footer {
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: 8px;
  }

  &__download {
    color: $gray-800;

    &:hover {
      color: $orange;
      text-decoration: none;
    }
  }
}

.detail {
  &__title {
    font-size: 24px;
    color: $orange;
    margin-bottom: 16px;
  }
}

.in-set-title {
  display: inline-block;
  font-size: 16px;
  border-bottom: 1px solid $border-color;
  padding-bottom: 4px;
}
