.section {
  margin-bottom: 60px;

  &__title {
    font-size: 30px;
    font-weight: 700;
    color: $orange;
    text-align: center;
    margin-bottom: 40px;
  }

  &__subtitle {
    font-size: 24px;
    text-align: center;
    margin: 40px 0 30px;

    a {
      color: $orange;

      &:hover {
        color: darken($orange, 10%);
        text-decoration: none;
      }
    }
  }

  @media (max-width: 767px) {
    &__title {
      font-size: 24px;
      margin-bottom: 20px;
    }

    &__subtitle {
      font-size: 20px;
      margin: 20px 0;
    }
  }
}

.section-2 {
  margin-bottom: 60px;

  &__title {
    text-align: center;
    color: $orange;
    font-size: 24px;
    margin-bottom: 30px;
  }

  @media (max-width: 767px) {
    &__title {
      font-size: 20px;
      margin-bottom: 30px;
    }
  }
}
