.menu {
  list-style: none;
  margin: 0;
  padding: 0;

  &-item {
    margin: 4px 0 8px;
    position: relative;
  }

  &-link {
    display: block;
    width: 100%;
    font-size: 14px;
    line-height: 40px;
    padding: 0 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: inherit;

    &:hover {
      color: inherit;
      text-decoration: none;
    }
  }

  &-toggle {
    display: inline-flex;
    width: 40px;
    height: 40px;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;

    span {
      display: inline-flex;
      width: 24px;
      height: 24px;
      justify-content: center;
      align-items: center;
      border-radius: 12px;
      transition: 0.3s;

      &:hover {
        background-color: rgba(#000, 0.1);
      }
    }

    &.active span {
      transform: rotate(90deg);
    }
  }

  &-sub {
    display: none;
  }

  &-sub &-link {
    padding-left: 40px;
  }

  &-sub-2 &-link {
    padding-left: 60px;
  }

  &-sub-3 &-link {
    padding-left: 80px;
  }

  &-item:hover > &-link {
    background-color: rgba($primary, 0.05);
  }

  &-mega-bg {
    display: none;
  }

  &-root > &-item > &-link {
    font-size: 18px;
  }

  @media (min-width: #{$navbar-breakpoint}px) {
    // menu root
    &-root {
      display: flex;
      box-shadow: inset 0 -1px 0 rgba($white, 0.5);
      margin-bottom: 40px;
      position: relative;
    }

    &-root > &-item {
      margin: 0;
      position: static;

      &:not(:last-child) {
        margin-right: 130px;
      }

      &:hover::after {
        transform: rotateY(0);
      }
    }

    &-root > &-item > &-link {
      font-size: 20px;
      color: #999;
      letter-spacing: 3px;
      padding: 0 !important;
      position: relative;

      &::after {
        content: "";
        display: block;
        width: 100%;
        border-bottom: 1px solid $white;
        position: absolute;
        bottom: 0px;
        left: 0;
        transform: rotateY(90deg);
        transition: 0.3s;
      }

      &.active {
        color: $white;

        &::after {
          transform: rotateY(0);
        }
      }
    }

    &-root > &-item:hover > &-link {
      background: 0;
      color: $white;

      &::after {
        transform: rotateY(0);
      }
    }

    &-root > &-item-group > &-toggle {
      display: none;
    }

    &-sub {
      display: none;
      width: 100%;
      position: absolute;
      z-index: 2;
      top: 100%;
      left: 0;

      &.active {
        display: flex;
      }
    }

    &-root > &-item-group:hover > &-sub {
      display: flex;
    }

    &-sub &-item {
      &:not(:last-child) {
        margin-right: 70px;
      }
    }

    &-sub &-link {
      padding: 0;
      white-space: nowrap;
      color: #999;

      &.active {
        color: $white;
      }
    }

    &-sub &-item:hover &-link {
      background: 0;
      color: $white;
    }
  }
}

@keyframes menu-float-show {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.h-menu {
  &__btn {
    display: none;
  }

  @media (min-width: 1200px) {
    &__btn {
      display: inline-block;
      font-size: 13px;
      font-weight: 700;
      text-transform: uppercase;
      cursor: pointer;
      position: relative;
      z-index: 1;
      line-height: 55px;
      padding: 0 20px 0 10px;

      &::after {
        content: "";
        display: block;
        width: 100%;
        height: 20px;
        position: absolute;
        top: 100%;
        left: 0;
      }
    }

    &__float {
      display: none;
      width: 246px;
      background-color: $white;
      position: absolute;
      z-index: 200;
      top: 100%;
      left: 0;
    }

    &__backdrop {
      display: none;
      width: 100vw;
      height: 200vh;
      position: absolute;
      top: 100%;
      left: 50%;
      right: 0;
      z-index: 100;
      transform: translateX(-50%);
      background-color: rgba(#000, 0.5);
    }

    &__content:hover &__float {
      display: block;
    }

    &__content:hover ~ &__backdrop {
      display: block;
    }
  }
}

body.is-home .h-menu__float {
  display: block;
}

body.is-home .h-menu__backdrop {
  display: none !important;
}
